<template>
  <div>
    <cms-breadcrumb to="/messageSet/systemTpl/list" @click="rest">
      <li>系统消息模板-新增</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt :collapse='false'>
        <li>设置系统发送消息的内容、方式</li>
      </cms-prompt>
      <el-form ref="form" :rules="saveRules" :model="formData" label-width="210px">
        <el-form-item label="模板标题" prop="mesTitle" :error='errorMessage.mesTitle'>
          <el-input v-model="formData.mesTitle" :autofocus="true" ref="first" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="模板标识" prop="mesCode" :error='errorMessage.mesCode'>
          <el-input v-model="formData.mesCode" maxlength="100"></el-input>
          <div class="gray">建议使用英文标识，全局唯一</div>
        </el-form-item>
        <el-form-item label="模板描述" prop="remark" :error='errorMessage.remark'>
          <el-input v-model="formData.remark" type="textarea" :rows="6" maxlength="200"></el-input>
        </el-form-item>
        <el-form-item label="是否启用" prop="details.isOpen">
          <cms-switch v-model="formData.details.isOpen" @change="changeOpen"></cms-switch>
        </el-form-item>
        <el-form-item label="模板内容" prop="details.mesContent"  ref="mesContent"
          :rules="formData.details.isOpen? [{ required: true, message: '模板内容必须填写', trigger: 'blur' }] :[] ">
          <el-input v-model="formData.details.mesContent" type="textarea" :rows="4" maxlength="150"></el-input>
        </el-form-item>
        <el-form-item label="提示文案" prop="details.extendedField">
          <el-input v-model="formData.details.extendedField" maxlength="200"></el-input>
        </el-form-item>
        </el-form>
        <div class="cms-form-footer">
          <cms-button @click="saveData(false)" v-color>保存并新增</cms-button>
          <cms-button @click="saveData(true)" v-color>保存</cms-button>
          <cms-button type='rest' @click="reset">重置</cms-button>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { messageTpSave, checkMesCode } from '@/api/setting/messageSet.js'
var defaultData = {
  mesCode: '',
  mesTitle: '',
  remark: '',
  details: {
    isOpen: false,
    mesType: '',
    mesContent: '',
    extendedField: ''
  }
}
export default {
  name: 'messageSetShopSave',
  mixins: [form],
  data() {
    var validateCode = (rule, value, callback) => { // 验证模板标识的唯一性
      checkMesCode({
        mesCode: this.formData.mesCode
      }).then(res => {
        if (res.code === 200) {
          callback()
        } else {
          callback(new Error('模板标识已存在，不允许重复绑定'))
        }
      })
    }
    return {
      activeName: '1',
      formData: Object.assign({}, defaultData),
      saveRules: {
        // 新增规则
        mesCode: [rules.required('请填写模板标识'), rules.custom(validateCode), rules.english(), rules.len(1, 150)],
        mesTitle: [rules.required('请填写模板标题'), rules.len(1, 150)],
        remark: [rules.len(1, 500)]
      }
    }
  },
  methods: {
    saveData(state) {
      this.formData.details.mesType = '4'
      const arr = []
      arr.push(this.formData.details)
      const params = {
        mesCode: this.formData.mesCode,
        mesTitle: this.formData.mesTitle,
        remark: this.formData.remark,
        mesType: 3,
        details: arr
      }
      this.saveForm(
        messageTpSave,
        params,
        state,
        '/messageSet/systemTpl/list',
        'list'
      )
    },
    changeTabs(tab, event) {
      this.activeName = tab.name
    },
    changeOpen(status) {
      if (!status) {
        this.$refs.mesContent.clearValidate()
      }
    },
    reset() {
      this.formData = Object.assign({}, defaultData)
      for (const key in this.formData.details) {
        this.formData.details[key] = ''
      }
      this.formData.details.isOpen = false
      this.$refs.form.resetFields()
    }
  }
}
</script>

<style>
</style>



